<template>
  <div class="home">
        <p>Files added {{ filescount }}</p><button @click="count">Count</button>
        <div></div>
        <button class="btn waves-effect waves-light btn-small" @click="addJson">
            Add JSON to DB
        </button>
        <button class="btn waves-effect waves-light btn-small" @click="addImage">
            Add Image to DB
        </button>
        <button class="btn waves-effect waves-light btn-small" @click="getE">
            Get first
        </button>
        <input v-model="input">
        <p>First: {{ first }} </p>
        <p></p>
        <button @click="clear">
            Clear
        </button>
        <button @click="bulkDelete">
            Bulk delete
        </button>
  </div>
</template>

<script>
const json = require('../../big3.json')
const img = require('../../img.jpg')

export default {
  name: 'test',
  data: () => ({
    jsonfile: JSON.stringify(json),
    image: img,
    counter: 0,
    first: undefined,
    input: undefined,
    filescount: 0
  }),
  computed: {
  },
  methods: {
    async addJson () {
      var n = this.input || 1
      for (let i = 0; i < n; i++) {
        await this.$db.files.put({ name: `file${this.counter}`, file: this.jsonfile })
        this.counter++
      }
    },
    async addImage () {
      await this.$db.files.put({ name: `file${this.counter}`, file: this.image })
      this.counter++
    },
    getE () {
      this.$db.files.orderBy('pk').first().then(e => { this.first = e })
    },
    count () {
      this.$db.files.count().then(e => { this.filescount = e })
    },
    clear () {
      this.$db.files.clear().then(this.$message('ok')).catch(e => this.error(e))
    },
    bulkDelete () {
      var i = this.input || 1
      this.$db.files.limit(i).delete().then(this.$message('ok')).catch(e => this.error(e))
    }
  },
  created () {
    this.getE()
  }
}
</script>
